'use client';

import { useState, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { format } from 'date-fns';
import { useAuth } from '@/hooks/useAuth';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Calendar } from '@/components/ui/calendar';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { toast } from 'sonner';

const ticketSchema = z.object({
  title: z.string().min(1, 'Title is required'),
  description: z.string().min(1, 'Description is required'),
  imageUrl: z.string().optional(),
});

const bookingSchema = z.object({
  type: z.enum(['punchlist', 'unit-acceptance']),
  preferredDate: z.date({
    required_error: 'Please select a preferred date',
  }),
});

type TicketFormData = z.infer<typeof ticketSchema>;
type BookingFormData = z.infer<typeof bookingSchema>;

interface Ticket {
  id: string;
  title: string;
  description: string;
  imageUrl?: string;
  status: 'pending' | 'in-progress' | 'resolved';
  createdAt: string;
}

interface Booking {
  id: string;
  type: 'punchlist' | 'unit-acceptance';
  preferredDate: string;
  status: 'pending' | 'approved' | 'rejected';
  adminNotes?: string;
  createdAt: string;
}

export default function DashboardPage() {
  const { user, logout } = useAuth();
  const [tickets, setTickets] = useState<Ticket[]>([]);
  const [bookings, setBookings] = useState<Booking[]>([]);
  const [isSubmittingTicket, setIsSubmittingTicket] = useState(false);
  const [isSubmittingBooking, setIsSubmittingBooking] = useState(false);
  const [selectedImage, setSelectedImage] = useState<File | null>(null);
  const [imagePreview, setImagePreview] = useState<string | null>(null);

  const ticketForm = useForm<TicketFormData>({
    resolver: zodResolver(ticketSchema),
    defaultValues: {
      title: '',
      description: '',
      imageUrl: '',
    },
  });

  const bookingForm = useForm<BookingFormData>({
    resolver: zodResolver(bookingSchema),
  });

  useEffect(() => {
    if (user) {
      fetchTickets();
      fetchBookings();
    }
  }, [user]);

  const fetchTickets = async () => {
    try {
      const token = localStorage.getItem('auth-token');
      const response = await fetch('/api/tickets', {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      if (response.ok) {
        const data = await response.json();
        setTickets(data.tickets);
      }
    } catch (error) {
      console.error('Failed to fetch tickets:', error);
    }
  };

  const fetchBookings = async () => {
    try {
      const token = localStorage.getItem('auth-token');
      const response = await fetch('/api/bookings', {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      if (response.ok) {
        const data = await response.json();
        setBookings(data.bookings);
      }
    } catch (error) {
      console.error('Failed to fetch bookings:', error);
    }
  };

  const handleImageChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      setSelectedImage(file);
      const reader = new FileReader();
      reader.onload = (e) => {
        setImagePreview(e.target?.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const onSubmitTicket = async (data: TicketFormData) => {
    setIsSubmittingTicket(true);
    try {
      const token = localStorage.getItem('auth-token');
      
      // For demo purposes, we'll use a placeholder image URL if an image is selected
      const ticketData = {
        ...data,
        imageUrl: selectedImage ? 'https://placehold.co/400x300?text=Property+Issue+Photo' : undefined,
      };

      const response = await fetch('/api/tickets', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`,
        },
        body: JSON.stringify(ticketData),
      });

      if (response.ok) {
        toast.success('Ticket submitted successfully!');
        ticketForm.reset();
        setSelectedImage(null);
        setImagePreview(null);
        fetchTickets();
      } else {
        const error = await response.json();
        toast.error(error.message || 'Failed to submit ticket');
      }
    } catch (error) {
      toast.error('Failed to submit ticket');
    } finally {
      setIsSubmittingTicket(false);
    }
  };

  const onSubmitBooking = async (data: BookingFormData) => {
    setIsSubmittingBooking(true);
    try {
      const token = localStorage.getItem('auth-token');
      const response = await fetch('/api/bookings', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`,
        },
        body: JSON.stringify({
          type: data.type,
          preferredDate: data.preferredDate.toISOString(),
        }),
      });

      if (response.ok) {
        toast.success('Booking request submitted successfully!');
        bookingForm.reset();
        fetchBookings();
      } else {
        const error = await response.json();
        toast.error(error.message || 'Failed to submit booking request');
      }
    } catch (error) {
      toast.error('Failed to submit booking request');
    } finally {
      setIsSubmittingBooking(false);
    }
  };

  const getStatusBadge = (status: string) => {
    const variants: Record<string, 'default' | 'secondary' | 'destructive'> = {
      pending: 'default',
      'in-progress': 'secondary',
      resolved: 'secondary',
      approved: 'secondary',
      rejected: 'destructive',
    };

    return (
      <Badge variant={variants[status] || 'default'}>
        {status.charAt(0).toUpperCase() + status.slice(1)}
      </Badge>
    );
  };

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary mx-auto mb-4"></div>
          <p className="text-muted-foreground">Loading...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-background">
      <header className="border-b bg-card">
        <div className="container mx-auto px-4 py-4 flex justify-between items-center">
          <div>
            <h1 className="text-2xl font-bold">Dashboard</h1>
            <p className="text-muted-foreground">
              Welcome back, {user.firstName} {user.lastName} - Block {user.blockNumber}, Lot {user.lotNumber}
            </p>
          </div>
          <Button variant="outline" onClick={logout}>
            Sign Out
          </Button>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        <Tabs defaultValue="submit" className="space-y-6">
          <TabsList className="grid w-full grid-cols-3">
            <TabsTrigger value="submit">Submit Requests</TabsTrigger>
            <TabsTrigger value="tickets">My Tickets</TabsTrigger>
            <TabsTrigger value="bookings">My Bookings</TabsTrigger>
          </TabsList>

          <TabsContent value="submit" className="space-y-6">
            <div className="grid md:grid-cols-2 gap-6">
              {/* Ticket Submission */}
              <Card>
                <CardHeader>
                  <CardTitle>Submit Property Ticket</CardTitle>
                  <CardDescription>
                    Report property issues or maintenance requests
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <Form {...ticketForm}>
                    <form onSubmit={ticketForm.handleSubmit(onSubmitTicket)} className="space-y-4">
                      <FormField
                        control={ticketForm.control}
                        name="title"
                        render={({ field }) => (
                          <FormItem>
                            <FormLabel>Issue Title</FormLabel>
                            <FormControl>
                              <Input placeholder="e.g., Leaky faucet in kitchen" {...field} />
                            </FormControl>
                            <FormMessage />
                          </FormItem>
                        )}
                      />

                      <FormField
                        control={ticketForm.control}
                        name="description"
                        render={({ field }) => (
                          <FormItem>
                            <FormLabel>Description</FormLabel>
                            <FormControl>
                              <Textarea 
                                placeholder="Please describe the issue in detail..."
                                className="min-h-[100px]"
                                {...field}
                              />
                            </FormControl>
                            <FormMessage />
                          </FormItem>
                        )}
                      />

                      <div>
                        <label className="block text-sm font-medium mb-2">
                          Photo (Optional)
                        </label>
                        <Input
                          type="file"
                          accept="image/*"
                          onChange={handleImageChange}
                          className="mb-2"
                        />
                        {imagePreview && (
                          <div className="mt-2">
                            <img
                              src="https://placehold.co/400x300?text=Property+Issue+Photo+Preview"
                              alt="Property issue photo preview"
                              className="w-full h-32 object-cover rounded border"
                              onError={(e) => {
                                e.currentTarget.style.display = 'none';
                              }}
                            />
                          </div>
                        )}
                      </div>

                      <Button type="submit" className="w-full" disabled={isSubmittingTicket}>
                        {isSubmittingTicket ? 'Submitting...' : 'Submit Ticket'}
                      </Button>
                    </form>
                  </Form>
                </CardContent>
              </Card>

              {/* Booking Submission */}
              <Card>
                <CardHeader>
                  <CardTitle>Book Appointment</CardTitle>
                  <CardDescription>
                    Schedule punchlist or unit acceptance appointments
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <Form {...bookingForm}>
                    <form onSubmit={bookingForm.handleSubmit(onSubmitBooking)} className="space-y-4">
                      <FormField
                        control={bookingForm.control}
                        name="type"
                        render={({ field }) => (
                          <FormItem>
                            <FormLabel>Appointment Type</FormLabel>
                            <Select onValueChange={field.onChange} defaultValue={field.value}>
                              <FormControl>
                                <SelectTrigger>
                                  <SelectValue placeholder="Select appointment type" />
                                </SelectTrigger>
                              </FormControl>
                              <SelectContent>
                                <SelectItem value="punchlist">Punchlist</SelectItem>
                                <SelectItem value="unit-acceptance">Unit Acceptance</SelectItem>
                              </SelectContent>
                            </Select>
                            <FormMessage />
                          </FormItem>
                        )}
                      />

                      <FormField
                        control={bookingForm.control}
                        name="preferredDate"
                        render={({ field }) => (
                          <FormItem className="flex flex-col">
                            <FormLabel>Preferred Date</FormLabel>
                            <Popover>
                              <PopoverTrigger asChild>
                                <FormControl>
                                  <Button
                                    variant="outline"
                                    className="w-full pl-3 text-left font-normal"
                                  >
                                    {field.value ? (
                                      format(field.value, "PPP")
                                    ) : (
                                      <span>Pick a date</span>
                                    )}
                                  </Button>
                                </FormControl>
                              </PopoverTrigger>
                              <PopoverContent className="w-auto p-0" align="start">
                                <Calendar
                                  mode="single"
                                  selected={field.value}
                                  onSelect={field.onChange}
                                  disabled={(date) =>
                                    date < new Date() || date < new Date("1900-01-01")
                                  }
                                  initialFocus
                                />
                              </PopoverContent>
                            </Popover>
                            <FormMessage />
                          </FormItem>
                        )}
                      />

                      <Button type="submit" className="w-full" disabled={isSubmittingBooking}>
                        {isSubmittingBooking ? 'Submitting...' : 'Submit Booking Request'}
                      </Button>
                    </form>
                  </Form>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          <TabsContent value="tickets">
            <Card>
              <CardHeader>
                <CardTitle>My Tickets</CardTitle>
                <CardDescription>
                  Track the status of your submitted tickets
                </CardDescription>
              </CardHeader>
              <CardContent>
                {tickets.length === 0 ? (
                  <p className="text-center text-muted-foreground py-8">
                    No tickets submitted yet. Submit your first ticket to get started.
                  </p>
                ) : (
                  <div className="space-y-4">
                    {tickets.map((ticket) => (
                      <div key={ticket.id} className="border rounded-lg p-4">
                        <div className="flex justify-between items-start mb-2">
                          <h3 className="font-semibold">{ticket.title}</h3>
                          {getStatusBadge(ticket.status)}
                        </div>
                        <p className="text-muted-foreground mb-2">{ticket.description}</p>
                        {ticket.imageUrl && (
                          <img
                            src={ticket.imageUrl}
                            alt="Ticket attachment"
                            className="w-32 h-24 object-cover rounded border mb-2"
                            onError={(e) => {
                              e.currentTarget.style.display = 'none';
                            }}
                          />
                        )}
                        <p className="text-sm text-muted-foreground">
                          Submitted: {format(new Date(ticket.createdAt), 'PPP')}
                        </p>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="bookings">
            <Card>
              <CardHeader>
                <CardTitle>My Bookings</CardTitle>
                <CardDescription>
                  View your appointment requests and their status
                </CardDescription>
              </CardHeader>
              <CardContent>
                {bookings.length === 0 ? (
                  <p className="text-center text-muted-foreground py-8">
                    No booking requests yet. Submit your first booking request to get started.
                  </p>
                ) : (
                  <div className="space-y-4">
                    {bookings.map((booking) => (
                      <div key={booking.id} className="border rounded-lg p-4">
                        <div className="flex justify-between items-start mb-2">
                          <h3 className="font-semibold capitalize">
                            {booking.type.replace('-', ' ')} Appointment
                          </h3>
                          {getStatusBadge(booking.status)}
                        </div>
                        <p className="text-muted-foreground mb-2">
                          Preferred Date: {format(new Date(booking.preferredDate), 'PPP')}
                        </p>
                        {booking.adminNotes && (
                          <p className="text-sm bg-muted p-2 rounded mb-2">
                            <strong>Admin Notes:</strong> {booking.adminNotes}
                          </p>
                        )}
                        <p className="text-sm text-muted-foreground">
                          Requested: {format(new Date(booking.createdAt), 'PPP')}
                        </p>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
